.modal-form {
  background-color: $light-blue;
  color: #ffffff;
}

.popup__container {
  display: none; /*временно*/
  position: fixed;

  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  width: 100%;
  height: 100%;
  padding: 54px 0 0 0;

  background-color: rgba(255, 255, 255, 0.5);
  z-index: 100;
}

.popup {
  position: relative;

  width: 490px;
  min-height: 605px;
  padding: 1px 0 0 39px;
  margin: 0 auto;

  &--show {
    display: block;
  }

  &__close {
    position: absolute;

    right: 20px;
    top: 20px;

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.modal-form__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 28px;
  line-height: 140.62%;

  margin-bottom: 13px;
}

.modal-form__text {
  width: 400px;
  margin: 12px 0 32px;
}

.modal-form input[type="text"],
.modal-form input[type="tel"] {
  width: 411px;
  height: 52px;
  padding: 0 0 0 24px;
  margin-bottom: 30px;

  background: rgba(250, 250, 250, 0.1);
  border: none;
  color: #ffffff;

  &::placeholder {
    font-family: "PT Mono", "Courier New", monospace;
    font-size: 16px;
    line-height: 22px;
    color: #ffffff;
  }
}

.modal-form textarea {
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", "Courier New", monospace;

  width: 411px;
  height: 104px;
  padding: 11px 24px 0;

  background: rgba(250, 250, 250, 0.1);
  border: none;
  color: #ffffff;
  resize: none;

  &::placeholder {
    font-family: "PT Mono", "Courier New", monospace;
    font-size: 16px;
    line-height: 22px;
    color: #ffffff;
  }
}

.modal-form__button {
  width: 411px;
  min-height: 47px;
  margin: 0 0 25px 0;

  &:hover {
    border: 1px solid #ffffff;
  }
}

.modal-form label {
  position: relative;

  display: block;

  font-size: 14px;
  line-height: 16px;

  width: 400px;
  padding: 8px 0 0 49px;
  opacity: 0.8;
}

.modal-form__data {
  margin: 0;
}

.modal-form__textarea {
  margin: 0 0 25px;
}

.modal-form__checkbox {
  margin: 0;

  & input + label::before {
    content: "";

    width: 16px;
    height: 16px;

    position: absolute;
    left: 0;
    top: 11px;

    background-image: url("../img/icon-checkbox.svg");
    background-repeat: no-repeat;
  }

  & input:checked + label::after {
    content: "";

    width: 8px;
    height: 5px;

    position: absolute;
    left: 4px;
    top: 15px;

    background-image: url("../img/icon-tick.svg");
    background-repeat: no-repeat;
  }
}
